.blocklyMainBackground {
    stroke: none;
}

/* fallback */
@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(../font/icons.woff2) format('woff2');
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -webkit-font-feature-settings: 'liga';
    -webkit-font-smoothing: antialiased;
}

*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background-color: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 5px;
}

.blocklyScrollbarHandle {
    fill: rgba(255, 255, 255, 0.2);
}

/* Toolbar */

.toolbar {
    background-color: rgb(60, 58, 56);
    color: #eee;
    display: flex;
}

.toolbar .item {
    padding: 10px 17px;
    cursor: pointer;
}

.toolbar .stretch {
    flex-grow: 1;
}

.toolbar .item:hover {
    background-color: rgb(90, 88, 87);
}

.blocklyTreeRow {
    padding-right: 0;
}

.blocklyTreeHandle {
    width: 30px;
    height: 80px;
    background-color: red;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.blocklyTreeHandle .blocklyTreeBar1, .blocklyTreeHandle .blocklyTreeBar2, .blocklyTreeHandle .blocklyTreeBar3 {
    content: '';
    position: absolute;
    top: 50%;
    left: 80%;
    transform: translate(-50%, -50%);
    width: 2px;
    height: 50%;
    opacity: 0.1;
}

.blocklyTreeHandle .blocklyTreeBar2 {
    left: 50%;
    height: 70%;
}

.blocklyTreeHandle .blocklyTreeBar3 {
    left: 20%;
    height: 90%;
}



.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;
    display: none;
}


.editor-container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;

    .editor-and-sidebar {
        flex: 1;
        display: flex;
        flex-direction: row;
    }

    #blockly-div {
        flex-grow: 1;
    }

    // .sidebar {
    //     width: 300px;
    // }

    .top-bar {
        background-color: #3C3A38;
        display: flex;
        padding: 10px 15px;
        align-items: center;
    }

    .nav {
        display: flex;
        align-items: center;
        justify-content: flex-end;


        > ul {
            display: flex;
            gap: 20px;
            align-items: center;
            margin: 0;
        }

        li { 
            list-style: none;
            display: inline-flex;
            align-items: center;
        }

        a {
            display: inline-block;
            text-decoration: none;
            font-size: 1.1rem;
            font-family: monospace, sans-serif;
        }

        .user { position: relative;display: inline-flex;align-items: center; }
        .user img {
            width: 40px;
            border-radius: 50%;
            cursor: pointer;
        }

        .btn {
            display: inline-flex;
            align-items: center;
            gap: 10px;
            padding: 5px 10px;
            font-size: 1rem;
            background-color: $color-background;
            border-radius: 4px;
        }
        
        .btn:not(#run) {
            padding: 7px 10px;
        }

        .signin {
            background-color: $color-accent;
            color: $color-background;
            border: 1px solid $color-accent;
            border-radius: 4px;
            font-size: 1rem;
            border: 1px solid $color-accent;
            padding: 6px 10px;
        }
    }

    .icon svg {
        fill: #fff;
        position: relative;
        top: 2px;
    }

    #run {
        color: $color-background;
        background-color: $color-accent;
        gap: 5px;
        font-size: 1rem;
    }



    .user-menu {
        position: absolute;
        top: 100%;
        right: -20px;
        display: flex;
        flex-direction: column;
        background-color: $color-background-light;
        z-index: 999;
        width: 200px;
        border: 1px solid rgba($color: #fff, $alpha: 0.1);
        box-shadow: 0 0 10px rgb(0 0 0 / 10%);
        padding: 10px 0;
        border-radius: 10px;
        display: none;

        hr {
            margin: 0;
            border: none;
            border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
        }

        a, div {
            padding: 10px 20px;
            transition: all .2s;
            width: 100%;
        }

        .message {
            font-weight: 300;
        }

        a:hover {
            background-color: rgba($color: #fff, $alpha: 0.1);
        }

        .signout {
            color: #E04A3A;
        }
    }

    .user-menu.active {
        display: block;
    }


    .title {
        padding: 0 20px;
        flex: 1;
    }
}
